import React from 'react'
import { Box } from '@chakra-ui/react'

const Main = ({ header, subheader, metadata, children }) => {
  return (
    <>
      <Box px={['2.5rem', '2.5rem', '5rem']} py={'2.5rem'}>
        {(header || subheader) && (
          <Box mb={'4rem'} textAlign="center">
            {header && (
              <Box
                pb={'0.5rem'}
                css={`
                  position: relative;
                  text-transform: uppercase;
                  font-weight: bold;

                  &:after {
                    content: '';
                    height: 1px;
                    width: 50px;
                    position: absolute;
                    bottom: 0;
                    left: 50%;
                    margin-left: -25px;
                    background: ${(props) =>
                      props.theme.light.colors.secondary};
                  }

                  @media (prefers-color-scheme: dark) {
                    &:after {
                      background: ${(props) =>
                        props.theme.dark.colors.secondary};
                    }
                  }
                `}
              >
                {header}
              </Box>
            )}
            {subheader && <Box mt={'0.5rem'}>{subheader}</Box>}
          </Box>
        )}
        {children}
      </Box>
    </>
  )
}

export default Main
