import React from 'react'
import styled from 'styled-components'
import { darken } from 'polished'
import { Box } from '@chakra-ui/react'

const PrevNextContainer = styled.div`
  margin-top: 3rem;
  margin-bottom: 3rem;
  text-align: center;
`

const Prev = styled.div`
  float: left;
  background-color: ${(props) => props.theme.light.colors.secondary};
  :hover:not(.disabled) {
    background-color: ${(props) => props.theme.dark.colors.primary};
    cursor: pointer;
  }

  @media (prefers-color-scheme: dark) {
    background-color: ${(props) =>
      darken(0.05, props.theme.dark.colors.secondary)};
    :hover:not(.disabled) {
      background-color: ${(props) => props.theme.dark.colors.primary};
    }
  }
`

const Next = styled.div`
  float: right;
  background-color: ${(props) =>
    darken(0.05, props.theme.light.colors.secondary)};
  :hover:not(.disabled) {
    cursor: pointer;
    background-color: ${(props) => props.theme.light.colors.primary};
  }

  @media (prefers-color-scheme: dark) {
    a {
      color: ${(props) => props.theme.dark.colors.primary};
    }
    background-color: ${(props) =>
      darken(0.05, props.theme.dark.colors.secondary)};
    :hover:not(.disabled) {
      background-color: ${(props) => props.theme.dark.colors.primary};
    }
  }
`

const PrevNext = ({
  prev,
  prevText,
  prevLinkHandler,
  next,
  nextText,
  nextLinkHandler,
}) => {
  return (
    <>
      {(prev || next) && (
        <PrevNextContainer className="row ">
          <Prev
            className={
              prev === null
                ? 'col-6 rounded-left disabled'
                : 'col-6 rounded-left'
            }
          >
            {prev && (
              <>
                <Box p="1em" onClick={() => prevLinkHandler(prev)}>
                  {prevText}
                </Box>
              </>
            )}
          </Prev>
          <Next
            className={
              next === null
                ? 'col-6 rounded-right disabled'
                : 'col-6 rounded-right'
            }
          >
            {next && (
              <>
                <Box p="1em" onClick={() => nextLinkHandler(next)}>
                  {nextText}
                </Box>
              </>
            )}
          </Next>
        </PrevNextContainer>
      )}
    </>
  )
}

export default PrevNext
